<template>
    <div class="wrap">
        <div class="title">마대 환생계산기</div>

        <div class="input_wrap">
            <div>정보입력</div>
            <br />
            님이 환생한 날짜
            <input type="date" v-model="rebirthDay" />
            <br />
            리모<input v-model="level" />
            <br />
            <button @click="levelSpeed">계산해줘</button>
            <br />
            <div>환생 진행률은 : {{ progress ? `${progress}%` : "" }}</div>
            <div>님의 속도는 : {{ speed }}</div>
        </div>

        <div class="info_wrap">
            <div>
                참고 : 5.4점대 (1달 예상) 2.7점대 (2달 예상)) 1.8점대 (3달 예상)
            </div>
        </div>
    </div>
</template>

<script>
import dayjs from "dayjs";
import levelData from "./assets/data";

export default {
    name: "App",
    data() {
        return {
            level: "",
            rebirthDay: "",
            afterDay: "",

            speed: "",
            progress: "",
        };
    },
    watch: {
        rebirthDay() {
            // console.log(this.rebirthDay);
        },
        level() {
            // console.log(this.level);
        },
    },
    methods: {
        levelSpeed() {
            if (!this.rebirthDay || !this.level) {
                alert("입력 안한게 있음!");
                return;
            }
            const nowDay = dayjs();
            nowDay.format("YYYY-MM-DD");
            this.afterDay = nowDay.diff(this.rebirthDay, "Day");
            const total = levelData.filter((el) => el.index == this.level);
            const totalLevel = total[0].total;

            // console.log(totalLevel, Number("162250"));

            this.progress = ((totalLevel / Number("162250")) * 100).toFixed(2);
            this.speed = (totalLevel / this.afterDay).toFixed(2) / 1000;
        },
    },
};
</script>

<style>
.wrap > div {
    margin: 30px;
}

.input_wrap {
    width: 30%;
    display: flex;
    flex-direction: column;
}
</style>
